
































export default{
	data(){
		return{
			phone:'',
			verificationCode:'',
			account:"",
			password:'',
			invitationCode:'',
			showTime:false,
			time:60
		}
	},
	methods:{
	// 获取手机验证码
		getPhone(){
			var that =  this;
			 if(this.phone == ''){
				 uni.showToast({
				 	icon:"none",
					title:"手机号码不能为空"
				 })
			 }
			//缺少接口
			this.showTime = true	
			var go = setInterval(function(){
				that.time --;
				if(that.time == 0){
				   that.showTime = false;
			       clearInterval(go);
				   that.time = 60;
				}
			},1000)

		},
	// 注册
		register(){
			 if(!(/^1[34578]\d{9}$/.test(this.phone))){
				uni.showToast({
					icon: 'none',
					title: '请输入正确的11位手机号'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.okPassword !=this.password){
				uni.showToast({
					icon: 'none',
					title: '密码不一致'
				});
				return;
			}
			if(this.verificationCode ==''){
				uni.showToast({
					icon: 'none',
					title: '请输入短信验证码'
				});
				return;
			}
			if(this.superInvitationCode ==''){
				uni.showToast({
					icon: 'none',
					title: '请输入邀请码'
				});
				return;
			}
			
			
			const data ={
				phone:this.phone,
				verificationCode:this.verificationCode,
				account:this.phone,
				password:this.password,
				superInvitationCode:this.superInvitationCode
			}
			const jsonString = {
				requestType:"registUserInfo",
				userInfo:data,
			}
			
			const param ={
				controllerRequestType:"loginControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			uni.request({
				url:'http://192.168.43.229:8080/ScreenTheWord/MainController.do?',
				method:'POST',
				header: {
					"content-type":"application/x-www-form-urlencoded"
				},
				data:param,
				success: (res) => {
					console.log(res)
					if(res.data.errorCode == '0000'){
						uni.showToast({
							icon:'none',
							title:'注册成功'
						})
					}else{
						uni.showToast({
							icon:'none',
							title:res.data.errorMessage
						})
					}
				},
				fail:(response) =>{
					uni.showToast({
						icon:'none',
						title:'注册失败'
					})
				}
			})
		}
	}
}



